.button {
  border: 1px solid #214640;
  padding: 10px;
  font-weight: bold;
  font-family: "Press Start 2P", cursive;
  margin-bottom: 10px;
  margin-right: 3px;
  margin-left: 10px;
  border-radius: 10px;
  height: 32px;
  width: 32px;
  line-height: 32px;
  display: inline-block;
  text-align: center;
}

.button:hover {
  background-color: #214640;
  cursor: pointer;
}

.space {
  min-width: 140px;
  display: inline-block;
}

.label {
  font-size: 20px;
}

.controls {
  display: inline-block;
}

.control {
  white-space: nowrap;
  text-align: left;
}

.center {
  text-align: center;
}
