* {
    padding: 0;
    margin: 0;
}

body {
    margin: 0;
    font-family: "Quicksand", sans-serif;
    background-color: #2a1e0e;
    color: #efd8a1;
}

h1,
h2 {
    color: #efb775;
    font-family: "Press Start 2P", cursive;
    padding-top: 20px;
    padding-bottom: 20px;
    text-align: center;
}

h1 {
    font-size: 42px;
}

h2 {
    padding-top: 10px;
    padding-bottom: 10px;
}

p {
    text-align: left;
}

hr {
    border-top: 1px solid #efd8a1;
    border-bottom: 0;
}

a,
:visited {
    color: #efd8a1;
}

a:hover {
    color: #efb775;
}

button {
    color: #efd8a1;
    font-size: 100%;
    font-family: "Press Start 2P", cursive;
    padding: 15px;
    border: 1px solid transparent;
    background-color: #214640;
    text-decoration: none;
    border-radius: 10px;
}

button:hover,
button:focus {
    cursor: pointer;
    outline: none;
    border: 1px solid #efb775;
}

.secondary {
    border: 1px solid #214640;
    background-color: #2a1e0e;
}

.secondary:hover,
.secondary:focus {
    padding: 15px;
    background-color: #214640;
    border: 1px solid #214640;
}

.blocks {
    text-align: center;
}

.block {
    display: inline-block;
    padding-right: 20px;
    padding-bottom: 20px;
    max-width: 500px;
    width: 100%;
    vertical-align: top;
}

.block p {
    text-align: justify;
}

.inline {
    display: inline-block;
}

.cta {
    text-align: center;
    padding-top: 30px;
}

.update {
    left: 0;
    right: 0;
    margin: 0 auto;
    position: fixed;
    top: 200px;
    max-width: 300px;
    padding: 2em;
    background: #2a1e0e;
    border-radius: 10px;
    border: 1px solid #214640;
    text-align: center;
}

.update button {
    margin-top: 2em;
    margin-right: 1em;
}